import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/views/Hello.vue'
import Works from '@/views/Works.vue'
import Contact from '@/views/Contact.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'hello',
      component: Hello,
      meta: {
        tab: 0,
      },
    },
    {
      path: '/works',
      name: 'works',
      component: Works,
      meta: {
        tab: 1,
      },
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
      meta: {
        tab: 2,
      },
    },
  ],
})



