<template lang="pug">
#app
  .logo(
    :class='{"tab-0": $route.meta.tab === 0}'
    @click="$router.replace({name: 'hello'})"
  )
  WebsiteHeader
  transition(:name="transitionName" mode="out-in")
    router-view
  WebsiteFooter
</template>

<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'

export default {
  components: {
    WebsiteHeader: Header,
    WebsiteFooter: Footer,
  },
  watch: {
    $route(to, from) {
      if (typeof from.meta.tab === 'undefined') return
      this.transitionName = from.meta.tab < to.meta.tab ? 'out-left' : 'out-right'
    },
  },
  data() {
    return {
      transitionName: 'fade',
    }
  },
}

</script>

<style lang="stylus">
*
  box-sizing border-box

body
  font-family  "PingFang SC","PingHei","STHeitiSC-Light", "tahoma", "Myriad Set Pro", "Lucida Grande", "Helvetica Neue", "Helvetica", "Arial", "Verdana", "sans-serif", "microsoft yahei"
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale

body, html
  height 100%
  margin 0
  min-height 480px

@font-face
  font-family 'FFMark'
  src: url('./assets/FFMark-Bold.woff2') format('woff2'),
       url('./assets/FFMark-Bold.woff') format('woff')
  font-weight bold
  font-style normal

@font-face
  font-family 'FFMark'
  src: url('./assets/FFMark-Regular.woff2') format('woff2'),
       url('./assets/FFMarkPro-Book.woff') format('woff2')
  font-weight normal
  font-style normal

@font-face {
  font-family 'NeoSans'
  src url(./assets/neo-sans.woff) format('woff'),
      url(./assets/neo-sans.woff2) format('woff2')
}
  

#app
  height 100%
  display flex
  flex-direction column
  .main
    flex 1

.fade-enter-active, .fade-leave-active
  transition all .2s ease

.fade-enter, .fade-leave-active
  opacity 0



.out-left-enter-active, .out-left-leave-active
  transition all 0.2s ease-out

.out-left-enter, .out-left-leave-active
  opacity 0

.out-left-enter-active
  transform translate3d(100px,0,0)

.out-left-enter-to
  transform translate3d(0,0,0)

.out-left-leave-active
  transform translate3d(-100px,0,0)




.out-right-enter-active, .out-right-leave-active
  transition all 0.2s ease-out

.out-right-enter, .out-right-leave-active
  opacity 0

.out-right-enter-active
  transform translate3d(-100px,0,0)

.out-right-enter-to
  transform translate3d(0,0,0)

.out-right-leave-active
  transform translate3d(100px,0,0)

</style>


<style lang='stylus' scoped>

.logo
  height 80px
  width 80px
  position relative
  top 10px
  margin 0 auto
  background-size auto 100%
  position relative
  background-position center
  background-repeat no-repeat
  background-image url("./assets/wz_avatar.png")
  transition background-image 0.2s ease
  z-index 10000
  cursor pointer

  @media(min-aspect-ratio: 4/3)
    // background-image url("../assets/wz_avatar.png")
    width 80px
    margin-left 80px
    top 72px
    background-position left

  &.tab-0
    background-image url("./assets/wz_avatar_white.png")
    @media(min-aspect-ratio: 4/3)
      position fixed
  
</style>


